<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
require("echarts/lib/component/axisPointer");
  export default {
    name: 'sl-bar',
    props: {
      className: {
        type: String,
        default: 'chart'
      },
      id: {
        type: String,
        default: 'chart'
      },
      width: {
        type: String,
        default: '200px'
      },
      height: {
        type: String,
        default: '200px'
      },
      getOrginData: {
        type: Function,
        default: _ => { return [] }
      },
      chartSetting: {
        type: Object,
        default: _ => { return {} }
      }
    },
    data() {
      return {
        chart: null
      }
    },
    mounted () {
      this.initChart()
      window.addEventListener('resize', function () {
        this.chart.resize()
      })
    },
    beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose()
      this.chart = null
    },
    methods: {
      async initChart () {
        this.chart = echarts.init(document.getElementById(this.id))
        let data = await this.getOrginData()
        let option = {
            color: ['#ea5a2e'],
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                },
                formatter: function (params) {
                    params = params[0];
                    var date = (params.dataIndex + 1);
                    // var date = params.dataIndex;
                    return (date > 12 ? 'PM ' : 'AM ') + date + ':00  ￥' + params.value;
                },
            },
            grid: {
                left: '-40',
                right: '0',
                bottom: '-20',
                height: "44px",
                containLabel: true,
            },
            xAxis: [
                {
                    type: 'category',
                    show: false,
                    data: [],
                    axisTick: {
                        alignWithLabel: true
                    },
                    splitLine: {
                        "show": false
                    },
                    axisLine: {
                        show: false
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value',
                    axisLine: {
                        show: false
                    },
                    splitLine: {
                        "show": false
                    },
                }
            ],
            series: [
                {
                    name: '今日订单应收',
                    type: 'bar',
                    show: false,
                    barWidth: '50%',
                    data: data.ParkingOrderdate
                }
            ]
        }
        const opt = Object.assign({}, option, this.chartSetting)
        this.chart.setOption(opt)
      }
    }
  
  }
</script>


