<template>
  <div :class="className" :id="id" :style="{height:height,width:width}"></div>
</template>
<script>
// 引入基本模板
let echarts = require('echarts/lib/echarts')
// 引入折线图图组件
require('echarts/lib/chart/line')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
  export default {
    name: 'sl-line',
    props: {
      className: {
        type: String,
        default: 'chart'
      },
      id: {
        type: String,
        default: 'chart'
      },
      width: {
        type: String,
        default: '200px'
      },
      height: {
        type: String,
        default: '200px'
      },
      getOrginData: {
          type: Function,
          default: () => { return [] }
      }
    },
    data() {
      return {
        chart: null
      }
    },
    mounted () {
      this.initChart()
      window.addEventListener('resize', function () {
        this.chart.resize()
      })
    },
    beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose()
      this.chart = null
    },
    methods: {
      async initChart () {
        this.chart = echarts.init(document.getElementById(this.id))
        let data = await this.getOrginData()
        this.chart.setOption({
          calculable: false,
          tooltip: {
              trigger: 'axis',
              formatter: function (params) {
                  params = params[0];
                  var date = new Date(params.name);
                  return (date.getHours() > 12 ? 'PM ' + date.getHours() + ':00' : 'AM ' + date.getHours() + ':00') + ' ￥' + params.value[1];
              },
              axisPointer: {
                  animation: false
              }
          },
          grid: {
              left: '-40',
              right: '0',
              bottom: '-30',
              height: "44px",
              containLabel: true
          },
          xAxis: [
              {
                type: 'time',
                boundaryGap: false,
                show: false
              }
          ],
          yAxis: [
              {
                  type: 'value',
                  show: false,
                  splitLine: {
                      show: false
                  }

              }
          ],
          series: [
              {
                name: '交易实收',
                type: 'line',
                symbol: 'none',  //这句就是去掉点的  
                smooth: true,  //这句就是让曲线变平滑的  
                symbolSize: 3,
                areaStyle: {
                    normal: {
                        color: '#0098fe'
                    }
                },
                data: data.data
              }
          ],
          color: ['#0098fe'],
        })
      }
    }
  
  }
</script>


