<style lang="less" scoped>
@import "../../assets/css/grad/grad.css";
@import "./home.css";
</style>
<template>
  <!-- 今日数据开始 -->
  <div class="home container-fluid">
    <div class="row" id="todayData">
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-8 text-left">
                今日订单应收（元）
              </div>
              <div class="col-xs-2  text-right">
                <i class="iconfont" title="今日停车订单应收取的停车费">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick" data-bind="click:checkTodayOrder">
              <span>￥</span>
              <span data-bind="text:todayorderIncome,attr:{title:todayorderIncome}">0</span>
            </div>
            <div class="row chart mar0">
              <histogram style="height:'44px';width:'100%'" :getOrginData = "getParkingData"></histogram>
            </div>
            <div class="row avrage mar0">
              当月订单应收
              <span class="money enbleclick" data-bind="text:monthorderIncome,click:checkMonthOrder">￥0</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-8 text-left">
                今日交易实收（元）
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="今日在平台缴费的实际收入">&#xe606;</i>

              </div>
            </div>
            <div class="row income text-center enbleclick" data-bind="click:checkTodayTrade">
              <span>￥</span>
              <span data-bind="text:todaytradeIncome,attr:{title:todaytradeIncome}"></span>
            </div>
            <div class="row mar0 chart">
             <line-Chart id = "getTodayTradeEchart" style="height:'44px';width:'100%'" :getOrginData = "getParkingData"></line-Chart>
            </div>
            <div class="row avrage mar0">
              当月交易实收：
              <span class="money enbleclick" data-bind="text:monthtradeIncome,click:checkMonthTrade">￥0</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-8 text-left">
                今日充值金额（元）
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="今日APP客户端充值的金额">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick" data-bind="click:checkTodayCharge">
              <span>￥</span>
              <span data-bind="text:todaychargeIncome,attr:{title:todaychargeIncome}"></span>
            </div>
            <div class="row mar0 chart">
              <div style="width:100%;height:100%" data-bind="echarts:getchargeEchart">
              </div>
            </div>
            <div class="row avrage mar0">
              当月充值金额：
              <span class="money enbleclick" data-bind="click:checkMonthCharge,text:monthchargeIncome">￥0</span>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-3 col-sm-6 col-xs-12">
        <div class="lab">
          <div class="container-fluid">
            <div class="row mar-t20 colora3 clearfix">
              <div class="col-xs-8 text-left">
                今日静态交通吸引量（辆）
              </div>
              <div class="col-xs-2 text-right">
                <i class="iconfont" title="今日停车量总数">&#xe606;</i>
              </div>
            </div>
            <div class="row income text-center enbleclick">
              <span data-bind="text:todayTransportAttract,attr:{title:todayTransportAttract}"></span>
            </div>
            <div class="row chart mar0">
              <div data-bind="echarts:getAttractionEchart" style="width:100%;height:100%"></div>
            </div>
            <div class="row avrage mar0">
              <span>泊位数：
                <span class="money" data-bind="text:spaceCount">0</span>
              </span>
              <span class="fr">车场数量：
                <span class="money" data-bind="text:parklotCount">0</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 今日数据结束 -->
  </div>
</template>
<script>
  import lineChart from 'components/base/charts/lineChart.vue'
  import histogram from 'components/base/charts/histogram.vue'
  import { getParkingIncome } from '@/api/home'
  export default {
    name: 'home',
    data () {
      return {
        tradeData: [],
        chartSettings: {},
        chartData: {}
      }
    },
    components:{ lineChart, histogram },
    methods: {
    // 获取今日订单
     getParkingData (){
       return new Promise((resolve, reject) => {
          getParkingIncome().then((res) => {
            const data = res.returnObject
              let list = [];
              let key 
              for(key in data.data) {
                    var obj = {
                      name: key,
                      value: [key, data.data[key].toFixed(2)]
                  }
                  list.push(obj)
              }
            list.push(obj)
            data.data = list
            resolve(data)
          }, err => reject(err))
          })
        }
    }
  }
</script>




