import fetch from '@/utils/fetch'
/**
 * 获取今日订单应收
 * @export
 * @returns 
 */
export function getParkingIncome () {
  return fetch({
    url: 'https://www.easy-mock.com/mock/59f1b06e4943e64bd135fdfb/witparking-op/home/parkingIncome',
    method: 'get'
  })
}


