import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  /* 后端支持可开 */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  base: '/home',
  routes: [
    {path: '/', component: resolve => require(['components/home/home.vue'], resolve), meta: {nav: ['首页']}}
  ]
})



